import React, {useEffect, useMemo, useState} from 'react';
import { GoogleMap, Marker, MarkerF, Autocomplete, useJsApiLoader, InfoWindow,StandaloneSearchBox  } from '@react-google-maps/api';
import styled from 'styled-components';

const mapContainerStyle = {
    width: '100%',
    height: '100vh',
};

const Input = styled.input`
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 12px;
    font-size: 14px;
    outline: none;
    text-overflow: ellipsis;
    border-radius: 50px;
    height: 50px;
    width: 500px;
    margin: 10px 0 0 10px; 
    border: 1px solid #ccc;
    &:placeholder{
        padding-left: 5px;
    }
    @media (max-width: 480px) {
        width: 100%;
        top: 50px;
        margin: 10px 0 0 0; 
    }

`;
const libraries = ["drawing", "places"];
const GoogleMapNew = ({location, setLocation, setListItems}) => {

    const [isOpen, setIsOpen] = useState(false);
    const [selectedPlace, setSelectedPlace] = useState(null);
    const [searchPlace, setSearchPlace] = useState(null);
    const markers = [
        { id: 1, type: 'restaurant', position: { lat: 30.924042604161592, lng: 75.86001447226563 } },
        { id: 2, type: 'park', position: { lat: 30.84979498311168, lng: 75.69247296835938 } },
        { id: 3, type: 'school', position: { lat: 30.85946221306515, lng: 75.89572003867188 } }
    ];
    // Define marker icons or colors for each type
    const markerIcons = {
        restaurant: 'https://maps.google.com/mapfiles/kml/pal2/icon2.png',
        park: 'https://maps.google.com/mapfiles/kml/paddle/grn-blank.png',
        school: 'https://maps.google.com/mapfiles/kml/paddle/blu-blank.png'
    };
    const google = window.google;
    //Map APi loader to show map
    const { isLoaded } = useJsApiLoader ({
        googleMapsApiKey: "AIzaSyAGA1CEPuU3YyWdd0aJbTyZ7BIQ6y_w9t4",
        libraries: ["places"],
    });

    const getCurrentLocation = (event) => {
        setIsOpen(!isOpen);
        setLocation({lat:event.latLng.lat(), lng: event.latLng.lng()})
        const latLng = event.latLng;
        const geocoder = new google.maps.Geocoder();
        geocoder.geocode({location: latLng}, (result, status)=>{
            if(status === "OK") {
                setSelectedPlace(result[0].formatted_address);
            }
        })
    }

    const addLocationToList = ()=>{
        setListItems(oldValues=>[...oldValues,{address: selectedPlace, lat: location.lat, lng:location.lng}]);
    }


    // get the current seleted places in the search box
    const handleOnPlacesChanged = (places) => {
        setLocation({lat: searchPlace.getPlaces()[0].geometry.location.lat(), lng:searchPlace.getPlaces()[0].geometry.location.lng()});
    };

    return(
        <>
            <div className="App">
            {
            !isLoaded ? (
                <h1>Loading...</h1>
                ) : (
                    
                        <GoogleMap
                            onClick={(event)=>{getCurrentLocation(event)}}
                            mapContainerStyle={mapContainerStyle}
                            center={location}
                            zoom={10}
                        >
                            <MarkerF
                                position={location} 
                            />
                            {markers.map(marker => (
                            <MarkerF
                                key={marker.id}
                                position={marker.position}
                                icon={{
                                url: markerIcons[marker.type],
                                scaledSize: new window.google.maps.Size(30, 30) // Adjust the size as needed
                                }}
                            />
                            ))}
                            {
                                selectedPlace && (
                                    <InfoWindow
                                        className="mb-5"
                                        position={location}
                                        onCloseClick={()=>{
                                            setSelectedPlace(null);
                                        }}
                                    >
                                        <div className='px-2'>
                                            <h3>Marker Information</h3>
                                            <p>Latitude: <strong>{location.lat} </strong> </p>
                                            <p>Longitude: <strong>{location.lng} </strong></p>
                                            <span>Address is</span>
                                            <p className='mt-1'>{selectedPlace}</p>
                                            <button className='btn btn-primary btn-sm' onClick={addLocationToList}>Save</button>
                                        </div>
                                    </InfoWindow>
                                )
                            }
                            <StandaloneSearchBox
                                onLoad={(searchBox) => {
                                    setSearchPlace(searchBox);
                                }}
                                onPlacesChanged={handleOnPlacesChanged}
                                >
                                <Input
                                    type="text"
                                    placeholder="Search for a location..."
                                />
                            </StandaloneSearchBox>
                        </GoogleMap>
            )}
            </div>
        </>
    )
}

export default GoogleMapNew;