import React from 'react';
import ListGroup from 'react-bootstrap/ListGroup';
import CloseIcon from '../../assets/icons/close.png';
import styled from 'styled-components';


const ListGroupEl = styled(ListGroup)`
    @media (max-width: 480px) {
        bottom: 0;
        height: 250px;
        overflow: scroll;
        position: absolute;
        width: 100%;
    }
`;

const ListItems = ({listItems, setListItems, setLocation}) => {

    //Remove list item
    const removeItem = (event,ind) => {
        event.stopPropagation();
        let newArr = listItems.filter((value, index) => {
            return  index !== ind;
        });
        setListItems(newArr);
    }

    //Set location to clicked city
    const revisitLocation = (index) =>{
        setLocation({lat: listItems[index].lat, lng:listItems[index].lng});
    }


    return(
        <>  
            <ListGroupEl defaultActiveKey="#link1" className='mt-4'>
                {listItems.map((item,index)=>{
                    return (
                        <ListGroup.Item action onClick={()=>{revisitLocation(index)}}>
                            <div className='d-flex justify-content-between align-items-center'>
                                {item.address}
                                <img src={CloseIcon} height="20" width="20" alt="close icon" title='Remove' onClick={(event)=>{removeItem(event,index)}} />
                            </div>
                        </ListGroup.Item>
                    )
                })}
                
                
            </ListGroupEl>
        </>
    )
}
export default ListItems;