import React,{useState, useEffect } from 'react';
import ListItems from './components/list-items/ListItems';
import 'bootstrap/dist/css/bootstrap.min.css';
import Map from './components/maps/GoogleMapNew';
import { Container,Col,Row } from 'react-bootstrap';
function App() {

  const [location,setLocation] = useState({
    lat: -34.9285,lng: 138.6078
  });
  const [listItems, setListItems] = useState([
  ]);


  const getGeoLocation = () => {
    navigator.geolocation.getCurrentPosition((position)=>{
        console.log("Got Geolocation", position.coords.latitude)
        setLocation({lat:position.coords.latitude, lng:position.coords.longitude})
    });
  }

  useEffect(()=>{
    getGeoLocation();
  },[]);


  return (
    <>
      <Container fluid>
      <Row>
        <Col md={9}>
          {/* Google Map component */}
        <Map
            location={location}
            setLocation={setLocation}
            listItems={listItems}
            setListItems={setListItems}
            />
          
        </Col>
        <Col md={3}> 
          
          {/* List component to show saved addresses */}
          <ListItems listItems={listItems} setListItems={setListItems} setLocation={setLocation} />
        </Col>
      </Row>
      </Container>
    </>
  );
}

export default App;
